<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/selfShops/list"  @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>带 * 为必填项</li>
        <li>可对自营店铺进行新增操作</li>
        <li>新增保存会同时新增一位会员用户信息（只获取姓名及店家帐号元素）</li>
      </cms-prompt>
      <el-form ref="form" :rules="saveRules" :model="formData" label-width="220px"  @submit.native.prevent>
        <el-form-item label="店铺名称" prop="storeName" :error='errorMessage.storeName'>
          <el-input v-model="formData.storeName" :autofocus="true" ref="first" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="店家账号" prop="memberUsername" :error='errorMessage.memberUsername'>
          <el-input v-model="formData.memberUsername"></el-input>
          <div class='gray'>用于登录商城会员中心</div>
        </el-form-item>
        <el-form-item label="商家账号" prop="storeUsername" :error='errorMessage.storeUsername'>
          <el-input v-model="formData.storeUsername"></el-input>
          <div class='gray'>用于登录商家中心</div>
        </el-form-item>
        <el-form-item label="姓名" prop="contactName" ref="contactName" :error='errorMessage.contactName' >
          <el-input v-model="formData.contactName"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="contactMobile"  :error='errorMessage.contactMobile'>
          <el-input v-model="formData.contactMobile" maxlength="11"></el-input>
          <div class='gray'>用于登录商家中心</div>
        </el-form-item>
        <el-form-item label="邮箱" prop="contactMail" :error='errorMessage.contactMail'>
          <el-input v-model="formData.contactMail" maxlength="50"></el-input>
          <div class='gray'>用于登录商家中心</div>
        </el-form-item>
        <el-form-item label="登录密码"  prop="pStr" :error='errorMessage.pStr'>
          <el-input v-model="formData.pStr" type="password" maxlength="16"></el-input>
          <div class='gray'>6-16位数字、字母组合</div>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPassword" :error='errorMessage.checkPassword'>
          <el-input v-model="formData.checkPassword" type="password" maxlength="16"></el-input>
        </el-form-item>
        <!-- <el-form-item label="是否允许商家开通微信公众号" prop="isWechatEnable">
          <cms-switch v-model="formData.isWechatEnable" ></cms-switch>
          <div class='gray'>设置店铺是否允许开通店铺公众号功能，只有在商城设置->扩展应用中公众号模式为部分启用，才能生效</div>
        </el-form-item> -->
      </el-form>
      <div class="cms-form-footer">
        <cms-button  @click="save(false)" v-color >新增并保存</cms-button>
        <cms-button @click="save(true)"  v-color >保存</cms-button>
        <cms-button type='rest' @click="rest">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { selfShopsSave, selfVaildName, checkUsername } from '@/api/shop/selfShops.js'
import {
  validName,
  validUsername,
  validPhone,
  validContactMail,
  checkEmail
} from '@/api/shop/shopManage.js'
import cmsMember from '@/components/cms/cmsMember.vue'
var defaultData = {
  'contactMail': '',
  'contactMobile': '',
  'contactName': '',
  'storeName': '',
  'storeUsername': '',
  'memberUsername': '',
  'pStr': '',
  'checkPassword': '',
  'isWechatEnable': false
}
export default {
  mixins: [form],
  data() {
    var ischeckName = (rule, value, callback) => { // 验证姓名
      selfVaildName({
        validContactName: this.formData.contactName
      }).then(res => {
        if (res.data) {
          callback()
        } else {
          callback(new Error('姓名已存在，不允许重复添加'))
        }
      })
    }

    // var isEmail = (rule, value, callback) => {
    //   checkEmail({
    //     email: this.formData.contactMail
    //   }).then(res => {
    //     if (res.data) {
    //       callback()
    //     } else {
    //       callback(new Error('联系人邮箱已存在，不允许重复添加'))
    //     }
    //   })
    // }
    var validContactMaill = (rule, value, callback) => {
      // 验证联系人邮箱唯一

      return new Promise((resolve, reject) => {
        validContactMail({
          validContactMail: this.formData.contactMail
        }).then(res => {
          if (res.data) {
            checkEmail({
              email: this.formData.contactMail
            }).then(res => {
              if (res.data) {
                callback()
              } else {
                callback(new Error('联系人邮箱已存在，不允许重复添加'))
              }
            })
          } else {
            callback(new Error('联系人邮箱已存在，不允许重复添加'))
            reject()
          }
        })
      })
    }
    var validStoreName = (rule, value, callback) => {
      // 验证店铺名称唯一
      validName({
        validName: this.formData.storeName
      }).then(res => {
        if (res.data) {
          callback()
        } else {
          callback(new Error('店铺名已存在，不允许重复添加'))
        }
      })
    }
    var validUserPhone = (rule, value, callback) => {
      // 验证手机号唯一
      validPhone({
        validPhone: this.formData.contactMobile
      }).then(res => {
        if (res.data) {
          callback()
        } else {
          callback(new Error('手机号已注册店铺，不允许重复添加'))
        }
      })
    }
    var validUsernam = (rule, value, callback) => {
      // 验证商家账号唯一
      validUsername({
        validUsername: this.formData.storeUsername
      }).then(res => {
        if (res.data) {
          callback()
        } else {
          callback(new Error('该商家账号已存在，不允许重复添加'))
        }
      })
    }
    var validMemberUser = (rule, value, callback) => {
      // 验证店家账号唯一
      checkUsername({
        username: this.formData.memberUsername
      }).then(res => {
        if (res.data) {
          callback()
        } else {
          callback(new Error('该店家账号已存在，不允许重复添加'))
        }
      })
    }
    var validatePass = (rule, value, callback) => {
      var patrnPass = /^[a-zA-Z\d]+$/
      var patrnCh = /^[u4e00-u9fa5]*$/
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (!patrnPass.test(value) && !patrnCh.test(value)) {
          callback(new Error('只能输入数字和大小写字母'))
        } else {
          callback()
        }
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        if (this.type === 'save') {
          callback(new Error('请再次输入密码'))
        } else {
          callback()
        }
      } else if (value !== this.formData.pStr) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      formData: Object.assign({}, defaultData),
      saveRules: {// 新增规则
        storeName: [rules.required('请填写店铺名称'), rules.len(1, 15), rules.custom(validStoreName)],
        memberUsername: [rules.required('请填写店家账号'), rules.len(1, 20), rules.custom(validMemberUser)],
        storeUsername: [rules.required('请填写商家账号'), rules.len(1, 20), rules.custom(validUsernam)],
        contactName: [rules.required('请填写姓名'), rules.len(1, 150), rules.custom(ischeckName)],
        contactMobile: [rules.required('请填写手机号'), rules.len(1, 150), rules.phone(), rules.custom(validUserPhone)],
        contactMail: [rules.required('请填写邮箱'), rules.len(1, 150), rules.email(), rules.custom(validContactMaill)],
        pStr: [rules.required('请输入密码'), rules.custom(validatePass), rules.len(6, 16)],
        checkPassword: [rules.required('请再次输入密码'), rules.custom(validatePass2)],
        isWechatEnable: [rules.required('请选择是否开通微信公众号')]
      }
    }
  },
  components: {
    cmsMember
  },
  methods: {
    save(state) { // 添加
      const params = this.$cms.deepCopy(this.formData)
      params.pStr = this.$cms.encrypt(params.pStr)
      delete params.checkPassword
      this.saveForm(selfShopsSave, params, state, '/selfShops/list', 'list')
    }
  },
  activated() {
    this.autoFocus()
    if (this.$route.query.type === 'save') {
      this.formData = Object.assign({}, defaultData)
    }
  }
}
</script>




